import styled from 'styled-components'
import { rhythm } from '../../utils/typography'

const Tag = styled.span`
  position: relative;
  display: inline-block;
  height: ${rhythm(1.5)};
  line-height: ${rhythm(1.5)};
  padding: 0 ${rhythm(0.2)};
  margin-right: ${rhythm(0.5)};

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    display: block;
    height: ${rhythm(0.1)};
    width: 100%;
    background-color: #000;
    transform: scale(0, 0);
    transition: 300ms;
  }

  &:hover {
    &::after {
      transform: scale(1, 1);
    }
  }
`

export default Tag